<template>
<div>
	<el-table
      :data="tableData"
      class="write">
      <el-table-column
        prop="time"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        prop="title"
        label="">
      </el-table-column>
      <el-table-column label="">
      <template scope="scope">
        <el-button
          size="small"
          @click="handleEdit(scope.row.id)"></el-button> 
        <el-button
          size="small"
          type="danger"
          @click="handleDelete(scope.row.id)"></el-button>
      </template>
    </el-table-column>
    </el-table>
    <router-link class="writeBtn" to="/user/write"></router-link> 
</div>
</template>

<script>
// var listp = [{
//             id: '1',
//             time: '2016-05-02',
//             name: '',
//             title: ' 1518 '
//           }, {
//             id: '2',
//             time: '2016-05-04',
//             name: '',
//             title: ' 1517 '
//           }, {
//             id: '3',
//             time: '2016-05-01',
//             name: '',
//             title: ' 1519 '
//           }, {
//             id: '4',
//             time: '2016-05-03',
//             name: '',
//             title: ' 1516 '
//           }];
import axios from 'axios'        
	export default{
		data() {
        return {
         tableData: ''
         
        }
      },
      mounted(){
        this.getArticle()
      },
      methods: {
        handleEdit: function(id){
          this.$router.push('/lists/'+id);
        },
        handleDelete: function(id){
          var _this = this;
          axios.post('/api/delete',{
            id: id
          })
          .then(function(result){
              alert("");
              _this.$router.push('/');
          })
          .catch(function(err){
            console.log(err);
          })
        },
        getArticle: function(){
          var _this = this;
          axios.get('/api/lists')
            .then(function(result){
              _this.tableData = result.data;
            })
            .catch(function(err){

            })
        }
      }
	}
</script>


