<template>
<div>
	<div class="top-logo">
      <a href=""><img src="../../images/logo.png" alt=""></a>
    </div>
    <div class="nav">
      <el-row>
      <el-col :span="16">
		    <el-menu class="nav-left" mode="horizontal" router>
        <el-menu-item index="/"></el-menu-item>
        <el-menu-item index="/user/article"></el-menu-item>
        <el-menu-item index="/user/life"></el-menu-item>
        <el-menu-item index="/user/write"></el-menu-item>
        <el-menu-item index="/user/login" :class="{logDisplay:!count}">/</el-menu-item>
        <el-menu-item index="" :class="{logDisplay:count}" @click="exitLogin()"></el-menu-item>
        </el-menu>
      </el-col>
       <el-col :span="8" class="nav-right">
        <el-input class="search" placeholder="" icon="search"></el-input>
      </el-col>
      </el-row>
    </div>
     <router-view></router-view>
  
    </div>
</template>

<script>
  import {mapGetters,mapActions} from 'vuex'
	export default {
		  data(){
        return {
          
        }
      },
     computed: mapGetters([
        'count'
      ]),
     methods: {
      exitLogin: function(){
        this.$store.dispatch('increment');  //
        this.$router.push('/');
      }
     }
	}
</script>

<style>
  .logDisplay{
    display: none;
  }
</style>


